*,
*::before,
*::after {
    padding: 0;
    margin: 0;
    border: 0;
    box-sizing: border-box;
}

a {
    text-decoration: none;
}

ul,
ol,
li {
    list-style: none;
}

img {
    vertical-align: top;
}

a {
    color: #33363c;
    text-decoration: none;
    }
    a:hover {
    color: #2F9BC1;
    text-decoration: none;
    }
    h1, h2, h3, h4, h5, h6 {
    font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
    color: #33363c;
    padding: 0;
    margin: 0 0 10px 0;
    line-height: 130%;
    font-weight: bold;
    word-wrap: break-word;
    }
    h1 {
    font-size: 28px;
    }
    h2 {
    font-size: 22px;
    }
    h3 {
    font-size: 20px;
    }
    h4 {
    font-size: 19px;
    }
    h5 {
    font-size: 18px;
    }
    h6 {
    font-size: 16px;
    }
    p {
    line-height: 21px;
    margin-bottom: 12px;
    }

html,
body {
    height: 100%;
}
body {
    line-height: 1;
    background: #f0f0ed;
    color: #999;
    margin: 0;
    padding: 0;
    font-family: Helvetica, Arial, sans-serif;
    font-size: 12.5px;
    font-weight: normal;
}
.wrapper {
    min-height: 100%;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    width: 1200px;
    margin: 0 auto;
}
