.sign-in__button {
    width: 100%;
    height: 45px;
    display: inline-block;
    font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
    background-color: #2f9bc1;
    color: #fff;
    font-weight: bold;
    padding: 9.5px 15px;
    margin: 0;
    border: 0;
    font-size: 13.5px;
    line-height: 1;
    cursor: pointer;
    border-radius: 4px;
    transition: all 0.3s ease 0s;
}
.sign-in__button:hover{
    background-color: #216c87;
}