.page__sidebar {
    width: 245px;
    font-size: 12px;
}
.sidebar-item {
    position: relative;
    width: 100%;
    padding: 20px;
    margin-bottom: 20px;
    background: #fff;
    border-radius: 8px;
}
.item-title {
    display: block;
    width: auto;
    color: #33363c;
    font-size: 12px;
    font-weight: bold;
    border-bottom: 1px solid #f0f0ed;
    padding: 0 20px 15px 20px;
    margin: 0 -20px 20px -20px;
}
.item-options {
    display: flex;
    flex-wrap: wrap;
    position: relative;
    color: rgba(0, 0, 0, 0);
    line-height: 1;
    font-size: 11px;
    margin-bottom: 20px;
    padding: 0;
}
.item-options a{
    background-color: #77af12;
    color: #fff;
    font-weight: bold;
    font-size: 10px;
    padding: 5px 8px;
    margin: 0 3px 2px 0;
    border: 0;
    line-height: 1;
    border-radius: 4px;
    transition: all 0.3s ease 0s;
}
.item-options a:hover {
    background-color: #679710;
}