.item-sidebar-left {
    display: flex;
    margin: 10px 0px 0px 0px;
}
.item-avatar {
}
.item-avatar img {
    width: 58px;
    height: 58px;
    padding-right: 10px;
}
.item-name {
    display: block;
    color: #33363c;
    font-weight: bold;
    font-size: 12px;
    line-height: 17px;
}
.item-meta {
    line-height: 1;
    opacity: 1;
    margin-top: 2.5px;
    padding: 4px 8px;
    line-height: 1;
    color: #6595af;
    font-weight: bold;
    display: inline-block;
    font-size: 10px;
    border-radius: 4px;
    background-color: #e4ecf1;
    opacity: 1;
}
