.header {
    top: 0;
    left: 0;
    width: 100%;
    margin-bottom: 30px;
    background: #33363c;
    border-bottom-right-radius: 8px;
    border-bottom-left-radius: 8px;
}
.header__container {
    padding: 15px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.header__logo img {
    width: 108px;
    height: 25px;
}
.menu__list {
    display: flex;
    /* flex-wrap: wrap; */
    /* column-gap: 46px;
    row-gap: 15px; */
}
.menu__link {
    color: #fff;
    font-size: 12px;
    padding: 10px 13px;
    font-weight: bold;
    transition: all 0.3s ease 0s;
}
.menu__link:hover {
    color: #fff;
    background: #26282d;
    border-radius: 8px;
}
.header__buttons {
    margin-left: 15px;
}
.button {
    display: inline-block;
    position: relative;
    font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
    color: #fff;
    font-weight: bold;
    padding: 9px 15px;
    font-size: 13px;
    border-radius: 4px;
    margin: 0 0 0 5px;
    background-color: #77af12;
    transition: all 0.3s ease 0s;
}
.button:hover {
    background-color: #679710;
    color: #fff;
}
.button-signup {
    background-color: #2f9bc1;
}
.button-signup:hover {
    background-color: #2a8cae;
}
