.item-search__input {
    width: 58%;
    margin-right: 2%;
    font-family: Helvetica, Arial, sans-serif;
    font-size: 12.5px;
    padding: 8px 10px;
    color: #999;
    border: 1px solid #f7f7f7;
    background: #f7f7f7;
    border-radius: 4px;
}
.item-search__button {
    display: inline-block;
    font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
    background-color: #2f9bc1;
    color: #fff;
    font-weight: bold;
    padding: 9.5px 15px;
    margin: 0;
    border: 0;
    font-size: 13.5px;
    line-height: 1;
    cursor: pointer;
    border-radius: 4px;
    transition: all 0.3s ease 0s;
}
.item-search__button:hover {
    background-color: #2a8cae;
}